'use client';

import { useState } from 'react';
import PublicHeader from '../../components/layout/PublicHeader';
import styles from './careers.module.css';

interface JobPosition {
    id: string;
    title: string;
    department: 'commercial' | 'marketing' | 'tech';
    type: 'CDI' | 'CDD' | 'Freelance' | 'Stage';
    location: string;
    description: string;
    requirements: string[];
    isRemote: boolean;
}

const jobPositions: JobPosition[] = [
    // Commercial
    {
        id: 'com-1',
        title: 'Responsable Commercial Comores',
        department: 'commercial',
        type: 'CDI',
        location: 'Moroni, Comores',
        description: 'Développer notre réseau de partenaires commerciaux.',
        requirements: ['Vente B2B', 'Réseau local', 'Autonomie'],
        isRemote: false,
    },
    {
        id: 'com-2',
        title: 'Business Developer Diaspora',
        department: 'commercial',
        type: 'CDI',
        location: 'Paris, France',
        description: 'Cibler et convertir la diaspora comorienne.',
        requirements: ['Chasseur', 'Evénementiel', 'Bilingue'],
        isRemote: true,
    },
    // Marketing
    {
        id: 'mkt-1',
        title: 'Community Manager',
        department: 'marketing',
        type: 'CDD',
        location: 'Remote',
        description: 'Animer nos communautés sur les réseaux sociaux.',
        requirements: ['Social Media', 'Copywriting', 'Créatif'],
        isRemote: true,
    },
    // Tech
    {
        id: 'tech-1',
        title: 'Développeur React Native',
        department: 'tech',
        type: 'CDI',
        location: 'Remote',
        description: 'Développement mobile cross-platform.',
        requirements: ['React Native', 'TypeScript', 'Expo'],
        isRemote: true,
    },
    {
        id: 'tech-2',
        title: 'Développeur Backend NestJS',
        department: 'tech',
        type: 'CDI',
        location: 'Remote',
        description: 'Architecture et API.',
        requirements: ['NestJS', 'PostgreSQL', 'Prisma'],
        isRemote: true,
    }
];

import CandidateAuthModal from '../../components/auth/CandidateAuthModal';
import { useAuth } from '../../contexts/AuthContext';

export default function CareersPage() {
    const [selectedJob, setSelectedJob] = useState<JobPosition | null>(null);
    const [filter, setFilter] = useState<'all' | 'commercial' | 'marketing' | 'tech'>('all');

    const [showApplyModal, setShowApplyModal] = useState(false);
    const [showAuthModal, setShowAuthModal] = useState(false); // Local auth modal state
    const { candidateUser } = useAuth();

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        phone: '',
        message: ''
    });

    const filteredJobs = filter === 'all'
        ? jobPositions
        : jobPositions.filter(job => job.department === filter);

    const handleApplyClick = (job: JobPosition) => {
        setSelectedJob(job);
        if (candidateUser) {
            setShowApplyModal(true);
            setFormData({ name: candidateUser.firstName + ' ' + candidateUser.lastName, email: candidateUser.email, phone: '', message: '' });
        } else {
            setShowAuthModal(true);
        }
    };

    const handleAuthSuccess = () => {
        // Called when user successfully logs in via the modal
        setShowApplyModal(true);
        // Pre-fill data if available (though context update might be async, usually fast enough for React state)
        // We'll trust the user fills it or we can fetch context again if needed inside the modal
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        if (!selectedJob) return;

        // Construct the email body with form data
        const subject = encodeURIComponent(`Candidature: ${selectedJob.title} - ${formData.name}`);
        const bodyContent = `
Candidature pour le poste : ${selectedJob.title}

--- INFORMATIONS CANDIDAT ---
Nom : ${formData.name}
Email : ${formData.email}
Téléphone : ${formData.phone}

--- MOTIVATION ---
${formData.message}

------------------------------------------------
⚠️ IMPORTANT : Veuillez joindre votre CV à cet email avant d'envoyer.
------------------------------------------------
        `.trim();

        const body = encodeURIComponent(bodyContent);

        // Open default mail client
        window.location.href = `mailto:jobs@komoralink.com?subject=${subject}&body=${body}`;

        // Close modal after a delay to let the action happen
        setTimeout(() => setShowApplyModal(false), 1000);
    };

    const handleMailto = () => {
        if (!selectedJob) return;
        const subject = encodeURIComponent(`Candidature: ${selectedJob.title}`);
        const body = encodeURIComponent(`Bonjour,\n\nJe souhaite postuler au poste de ${selectedJob.title}.\n\nVeuillez trouver ci-joint mon CV.\n\nCordialement,`);
        window.location.href = `mailto:jobs@komoralink.com?subject=${subject}&body=${body}`;
    };

    return (
        <div style={{ background: 'var(--color-gray-50)', minHeight: '100vh', paddingTop: '70px' }}>
            <PublicHeader />

            {/* Application Modal with Form */}
            {showApplyModal && selectedJob && (
                <div style={{
                    position: 'fixed',
                    inset: 0,
                    background: 'rgba(0,0,0,0.6)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    zIndex: 1000,
                    backdropFilter: 'blur(4px)',
                    padding: '24px'
                }} onClick={() => setShowApplyModal(false)}>
                    <div style={{
                        background: 'white',
                        padding: '32px',
                        borderRadius: '24px',
                        maxWidth: '550px',
                        width: '100%',
                        position: 'relative',
                        boxShadow: 'var(--shadow-xl)',
                        maxHeight: '90vh',
                        overflowY: 'auto'
                    }} onClick={e => e.stopPropagation()}>

                        <div style={{ marginBottom: '24px' }}>
                            <h3 style={{ fontSize: '24px', fontWeight: 'bold', color: '#111827', marginBottom: '4px' }}>
                                Postuler
                            </h3>
                            <p style={{ color: '#6B7280' }}>
                                Poste : <strong style={{ color: 'var(--color-primary)' }}>{selectedJob.title}</strong>
                            </p>
                        </div>

                        <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
                            <div>
                                <label style={{ display: 'block', textTransform: 'uppercase', fontSize: '11px', fontWeight: '700', color: '#6B7280', marginBottom: '6px' }}>
                                    Nom complet <span style={{ color: '#EF4444' }}>*</span>
                                </label>
                                <input
                                    required
                                    type="text"
                                    placeholder="Votre Nom et Prénom"
                                    value={formData.name}
                                    onChange={e => setFormData({ ...formData, name: e.target.value })}
                                    style={{
                                        width: '100%',
                                        padding: '12px 16px',
                                        background: '#F9FAFB',
                                        border: '1px solid #E5E7EB',
                                        borderRadius: '12px',
                                        fontSize: '15px',
                                        outline: 'none',
                                        transition: 'border-color 0.2s'
                                    }}
                                />
                            </div>

                            <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '16px' }}>
                                <div>
                                    <label style={{ display: 'block', textTransform: 'uppercase', fontSize: '11px', fontWeight: '700', color: '#6B7280', marginBottom: '6px' }}>
                                        Email <span style={{ color: '#EF4444' }}>*</span>
                                    </label>
                                    <input
                                        required
                                        type="email"
                                        placeholder="email@exemple.com"
                                        value={formData.email}
                                        onChange={e => setFormData({ ...formData, email: e.target.value })}
                                        style={{
                                            width: '100%',
                                            padding: '12px 16px',
                                            background: '#F9FAFB',
                                            border: '1px solid #E5E7EB',
                                            borderRadius: '12px',
                                            fontSize: '15px',
                                            outline: 'none'
                                        }}
                                    />
                                </div>
                                <div>
                                    <label style={{ display: 'block', textTransform: 'uppercase', fontSize: '11px', fontWeight: '700', color: '#6B7280', marginBottom: '6px' }}>
                                        Téléphone <span style={{ color: '#EF4444' }}>*</span>
                                    </label>
                                    <input
                                        required
                                        type="tel"
                                        placeholder="+269 ..."
                                        value={formData.phone}
                                        onChange={e => setFormData({ ...formData, phone: e.target.value })}
                                        style={{
                                            width: '100%',
                                            padding: '12px 16px',
                                            background: '#F9FAFB',
                                            border: '1px solid #E5E7EB',
                                            borderRadius: '12px',
                                            fontSize: '15px',
                                            outline: 'none'
                                        }}
                                    />
                                </div>
                            </div>

                            <div>
                                <label style={{ display: 'block', textTransform: 'uppercase', fontSize: '11px', fontWeight: '700', color: '#6B7280', marginBottom: '6px' }}>
                                    Message / Motivation <span style={{ color: '#EF4444' }}>*</span>
                                </label>
                                <textarea
                                    required
                                    rows={4}
                                    placeholder="Présentez-vous en quelques lignes..."
                                    value={formData.message}
                                    onChange={e => setFormData({ ...formData, message: e.target.value })}
                                    style={{
                                        width: '100%',
                                        padding: '12px 16px',
                                        background: '#F9FAFB',
                                        border: '1px solid #E5E7EB',
                                        borderRadius: '12px',
                                        fontSize: '15px',
                                        outline: 'none',
                                        resize: 'vertical',
                                        minHeight: '100px',
                                        fontFamily: 'inherit'
                                    }}
                                />
                            </div>

                            <div style={{
                                background: '#F3F4F6',
                                borderRadius: '12px',
                                padding: '16px',
                                fontSize: '14px',
                                color: '#4B5563',
                                display: 'flex',
                                gap: '12px',
                                alignItems: 'flex-start',
                                lineHeight: '1.5'
                            }}>
                                <span style={{ fontSize: '18px' }}>ℹ️</span>
                                <div>
                                    Votre client mail s'ouvrira avec ces infos.
                                    <br />
                                    <strong>N'oubliez pas de joindre votre CV</strong> avant d'envoyer à <span style={{ color: 'var(--color-primary)', fontWeight: '600' }}>jobs@komoralink.com</span>.
                                </div>
                            </div>

                            <button type="submit" style={{
                                width: '100%',
                                padding: '16px',
                                background: 'var(--color-primary)',
                                color: 'white',
                                border: 'none',
                                borderRadius: '12px',
                                fontWeight: '700',
                                fontSize: '16px',
                                cursor: 'pointer',
                                transition: 'all 0.2s',
                                boxShadow: '0 4px 12px rgba(27, 184, 116, 0.3)',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                gap: '8px'
                            }}>
                                Continuer <span style={{ opacity: 0.8 }}>→</span>
                            </button>
                        </form>

                        <button onClick={() => setShowApplyModal(false)} style={{
                            position: 'absolute',
                            top: '20px',
                            right: '20px',
                            background: '#F3F4F6',
                            border: 'none',
                            width: '32px',
                            height: '32px',
                            borderRadius: '50%',
                            color: '#6B7280',
                            cursor: 'pointer',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                        }}>✕</button>
                    </div>
                </div>
            )}

            {/* Hero Section */}
            <div className={styles.hero}>
                <div className={styles.heroContent}>
                    <h1 className={styles.heroTitle}>
                        Rejoignez <span className={styles.accent}>l'aventure</span>
                    </h1>
                    <p className={styles.heroDescription}>
                        Construisons ensemble le futur du commerce aux Comores.
                        Talents passionnés bienvenus.
                    </p>
                </div>
            </div>

            {/* Filters */}
            <div className={styles.filtersContainer}>
                <button
                    onClick={() => setFilter('all')}
                    className={`${styles.filterBtn} ${filter === 'all' ? styles.active : ''}`}
                >
                    Tous
                </button>
                <button
                    onClick={() => setFilter('commercial')}
                    className={`${styles.filterBtn} ${filter === 'commercial' ? styles.active : ''}`}
                >
                    Commercial
                </button>
                <button
                    onClick={() => setFilter('marketing')}
                    className={`${styles.filterBtn} ${filter === 'marketing' ? styles.active : ''}`}
                >
                    Marketing
                </button>
                <button
                    onClick={() => setFilter('tech')}
                    className={`${styles.filterBtn} ${filter === 'tech' ? styles.active : ''}`}
                >
                    Tech
                </button>
            </div>

            {/* Jobs Grid */}
            <div className={styles.jobsGrid}>
                {filteredJobs.map((job) => (
                    <div key={job.id} className={styles.jobCard}>
                        <div className={styles.departmentBadge}>
                            {job.department} • {job.type}
                        </div>

                        <h3 className={styles.jobTitle}>{job.title}</h3>

                        <div className={styles.jobMeta}>
                            <span>📍 {job.location}</span>
                            {job.isRemote && <span style={{ color: 'var(--color-primary)' }}>• Remote</span>}
                        </div>

                        <p className={styles.jobDescription}>{job.description}</p>

                        <div className={styles.tags}>
                            {job.requirements.map((req, idx) => (
                                <span key={idx} className={styles.tag}>{req}</span>
                            ))}
                        </div>

                        <button
                            onClick={() => handleApplyClick(job)}
                            className={styles.applyBtn}
                            style={{ background: 'var(--color-gray-900)' }} // Keep button dark for contrast
                        >
                            Postuler maintenant <span>→</span>
                        </button>
                    </div>
                ))}
            </div>

            {filteredJobs.length === 0 && (
                <div className={styles.emptyState}>
                    <p>Aucun poste disponible dans cette catégorie.</p>
                    <button onClick={() => setFilter('all')} className={styles.emptyLink}>
                        Voir toutes les offres
                    </button>
                </div>
            )}

            <CandidateAuthModal
                isOpen={showAuthModal}
                onClose={() => setShowAuthModal(false)}
                onSuccess={handleAuthSuccess}
            />
        </div>
    );
}
